import React from "react"
// Libs
import getPageData from "globalLibs/helpers/get-page-data"
import getWorkExperience from "projectLibs/helpers/get-work-experience"
// Atoms
import Seo from "globalAtoms/Seo"
// Organisms
import Layout from "globalOrganisms/Layout"
import HomePageCover from "projectOrganisms/HomePageCover"
import Sidebar from "projectOrganisms/Sidebar"
import MainNav from "projectOrganisms/MainNav"

export default function IndexPage(props) {
  const pageData = getPageData(props)

  return (
    <Layout className="flex-col">
      <Seo pageData={pageData} />
      <Sidebar />
      <MainNav />
      <HomePageCover style={{ height: "400px" }} avatar>
        <div
          className="flex flex-col text-center absolute z-10 inset-0 text-hight-contrast"
          style={{ top: "24%" }}
        >
          <span className="xxxLarge-high-title font-bold mb-4">
            Senior UX/UI Designer
          </span>
          <span className="xxxLarge-h1">Middle Web Developer</span>
        </div>
      </HomePageCover>
      <article className="container mx-auto max-w-screen-lg mt-32">
        <div className="flex justify-between items-end">
          <h1 className="m-0">My way</h1>
          <span className="text-tint-foreground-orange">
            {getWorkExperience()} years of experience
          </span>
        </div>
        <hr />
        <ul>
          <li>
            2008 - 2015 - Received education. Work on freelance exchanges.
            Cooperation with Ukrainian and Russian web studios.
          </li>
          <li>
            2015 - 2016 - Worked in a Belarusian web studio as Middle UX/UI
            Designer.
          </li>
          <li>
            2016 - I decided to establish cooperation with regular customers and
            distance myself from freelance exchanges. To do this, I started
            pages on behance and dribble, and also started a personal blog.
            <br />
            <br />
            After six months, I managed to occupy almost all of my working time
            working with regular customers.
            <br />
            <br />
            Most of all, it was the freelance exchanges that helped me in this.
            <br />
            <br />
            At the end of 2016, I gave up running a personal blog, as my
            articles began to be unceremoniously stolen by publications such as
            Medium.
            <br />
            <br />
            I also refused to use all social networks except Linkedin.
            <br />
            <br />
            After weighing all the pros and cons, it became obvious that it is
            not economically profitable for me to attract customers through
            dribbble and behance. Customers from these social networks often
            wanted me to design for the sake of design. I specialized in
            conversion optimization and solving real business problems.
          </li>
          <li>
            2017 - In the first half of the year, I started developing my own
            design system. He also deepened his knowledge in the field of user
            behavior analysis and conversion optimization for e-commerce sites.
            <br />
            <br />
            In the second half of the year, the first release of my design
            system took place, which was called Sitis Design System, further
            (SDS). Over the next six months of using it, my income tripled. I
            will describe its SDS functionality:
            <ul className="list-disc list-inside ml-4">
              <li>
                Optimization of work on routine tasks (UI Kit with fully
                customizable components for any project, automatic export of all
                icons in the project, various presets, etc.). As a result, the
                time spent working on UX/UI decreased by at least 400% (!). The
                more complex the project was, the more optimization was felt.
              </li>
              <li>
                Automation of communication with developers. Now a repository on
                GitHub was automatically created for each project. All the
                project data and files were stored in it. No one else needed to
                search in messengers for links to the design, source code, files
                with project icons, etc. Everything was in one place and
                available at any time.
              </li>
              <li>
                Semi-automatic work with branding. Automatic generation of
                guidelines based on identity.
              </li>
              <li>
                A substantial simplification of the continued support of
                projects. Customers and programmers could request design changes
                in a few clicks. The history of changes was kept. As soon as I
                finished working on the next edit or innovation, all project
                participants received a notification by email.
              </li>
            </ul>
          </li>
          <li>
            2018 - Began to specialize in the most complex projects. Developed
            UX/UI for mapping services in Ukraine and Belarus. Perfectly
            mastered Google and Apple Guidlines. Added a lot of patterns to my
            design system for developing mobile app design. Continued to work
            for Upwork.
          </li>
          <li>
            2019 - Deepened my knowledge in Front-end development. I learned the
            Bottstrap framework, started learning Java Script, React and Node
            JS. I transferred my design system from Sketch to Figma,
            significantly expanded its capabilities and applications. Refused to
            work for Upwork in favor of working with regular clients.
          </li>
          <li>
            2020 - Continued to learn Java Script and its frameworks. I wrote a
            component library in React for my design system. Moved most of my
            projects to my own codebase. He continued to cooperate with regular
            customers.
          </li>
          <li className="text-tint-foreground-green">
            <span>NOW</span> - Started learning Python.
          </li>
        </ul>
      </article>
    </Layout>
  )
}
